.paginate {
    display: flex;
    justify-content: center;
}

.list {
    list-style: none;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0px;
    display: flex;
    flex-wrap: wrap;
}

.item {
    width: 25px;
    height: 25px;
    border: 1px solid gray;
    margin-right: 10px;
    margin-bottom: 10px;
}

.item:last-child {
margin-right: 0px;
}


.item:hover {
    border: 1px solid #84D981;
}

.link {
        display: flex;
        justify-content: center;
        align-items: center;
    width: 100%;
    height: 100%;
   text-decoration: none; 
    color: gray;
}